import React, { useContext } from "react";
import CartContext from "./CartContext";
import {
  Button,
  Flex,
  Grid,
  Heading,
  Link,
  Text,
  TextField,
} from "@radix-ui/themes";

const addressDetails = {
  billing_address_first_name: "Umakant",
  billing_address_last_name: "Vashishtha",
  billing_address_line1: "221B Baker Street",
  billing_address_line2: "Koramangala 8th Block",
  billing_address_line3: "",
  billing_address_phone: "1234567890",
  billing_address_city: "Bangalore",
  billing_address_state: "KA",
  billing_address_country: "India",
  billing_address_postal_code: "560095",
  billing_address_country_code_iso: "IN",
};

function capitalize(string) {
  return string.charAt(0).toUpperCase() + string.slice(1);
}

function sanitize(string) {
  while (string.indexOf("_") !== -1) {
    string = string.replace("_", " ");
  }
  return string.split(" ").map(capitalize).join(" ");
}

const Cart = () => {
  const { loading, items: cartItems, totalAmount } = useContext(CartContext);

  const totalItems = cartItems.reduce((acc, item) => acc + item.quantity, 0);

  return (
    <Flex direction="column" gap="4" style={{background: "rgb(229, 241, 255)", padding: "2rem", borderRadius: 20, boxShadow: "rgba(0, 0, 0, 0.5) 0px 0px 40px 0px"}}>
      {loading && <p>Loading...</p>}
      <Heading as="h1" style={{ fontSize: "2rem" }}>
        Place Order
      </Heading>
      <Text as="p" style={{ fontWeight: "bold" }}>
        Total Items: {totalItems}
      </Text>
      <Text as="p" style={{ fontWeight: "bold" }}>
        Total Amount:  {totalAmount.toFixed(2)}
      </Text>
      {totalItems > 0 ? (
        <div>
          <Heading as="h1" style={{marginBottom: 10}}>Address Details</Heading>
          <Grid columns="2" gap="2">
            {Object.entries(addressDetails).map(([key, value], i) => {
              return (
                  <Flex direction="column" gap="2" key={i}>
                    <Text key={i} as="label" for={key}>
                      {sanitize(key)}
                    </Text>
                    <TextField.Root key={i}>
                      <TextField.Input
                        placeholder={sanitize(key)}
                        id={key}
                        name={key}
                        defaultValue={value}
                      />
                    </TextField.Root>
                  </Flex>
              );
            })}
          </Grid>

          <p>Click on 'Checkout' to pay and place order</p>
        </div>
      ) : (
        <p>The cart is currently empty.</p>
      )}

      <div>
        <Link href="/checkout">
          <Button size={"4"} disabled={totalAmount <= 0}>
            Checkout
          </Button>
        </Link>
      </div>
    </Flex>
  );
};

export default Cart;
