import React, { useContext, useEffect } from "react";
import { Button, Flex, Grid, Heading, Link, Text } from "@radix-ui/themes";
import { getProductList } from "../../ApiClient/products";
import Product from "./Product";
import CartContext from "../Cart/CartContext";

export default function ProductList() {
  const { totalAmount } = useContext(CartContext);
  const [products, setProducts] = React.useState([]);

  useEffect(() => {
    getProductList().then((response) => {
      setProducts(response.data);
    });
  }, []);

  return (
    <Flex direction="column" gap="4" style={{background: "rgb(229, 241, 255)", padding: "2rem", borderRadius: 20, boxShadow: "rgba(0, 0, 0, 0.5) 0px 0px 40px 0px"}}>
      <Heading as="h1" style={{fontSize: "2rem"}}>Products</Heading>
      <Grid columns="3">
        {products.map((product) => (
          <Product key={product.id} product={product} />
        ))}
      </Grid>

      <Text as="span" style={{ fontWeight: "bolder" }}>
        Your Total:  {totalAmount}
      </Text>

      <div>
        <Link href="/cart">
          <Button size={"4"}>Go To Cart</Button>
        </Link>
      </div>
    </Flex>
  );
}
