import apiClient from ".";

export async function createOrder() {
  const response = await apiClient.post("/payment/order");
  return response.data;
}

export async function checkoutCart({
  orderId,
  cardNumber,
  cardExpiryMonth,
  cardExpiryYear,
  cardSecurityCode,
  nameOnCard,
}) {
  const response = await apiClient.post("/payment/pay_via_card", {
    orderId,
    cardNumber,
    cardExpiryMonth,
    cardExpiryYear,
    cardSecurityCode,
    nameOnCard,
  });

  return response.data;
}
