import React, { useContext, useEffect, useReducer } from "react";
import CartContext from "../Cart/CartContext";
import {
  Button,
  Flex,
  Select,
  Heading,
  Text,
  TextField,
  Grid,
} from "@radix-ui/themes";
import { checkoutCart, createOrder } from "../../ApiClient/checkout";
import visa from '../../images/visa.png';
import masterCard from '../../images/master-card.png';

const reducer = (state, action) => {
  return {
    ...state,
    [action.name]: action.value,
  };
};

const Checkout = () => {
  const { loading, setLoading, totalAmount, clearCart } =
    useContext(CartContext);

  const [paymentDetails, updatePaymentDetails] = useReducer(reducer, {
    orderId: "",
    cardNumber: "4242424242424242",
    cardSecurityCode: "111",
    nameOnCard: "Name",
    cardExpiryMonth: "10",
    cardExpiryYear: "2024",
  });

  useEffect(() => {
    setLoading(true);

    createOrder()
      .then((response) => {
        updatePaymentDetails({ name: "orderId", value: response.data.orderId });
      })
      .finally(() => setLoading(false));
  }, [setLoading]);

  const checkoutDisabled =
    totalAmount <= 0 || loading || !paymentDetails.orderId;

  const initiatePayment = async () => {
    setLoading(true);
    const {
      data: { paymentUrl },
    } = await checkoutCart(paymentDetails);

    setLoading(false);

    clearCart();

    window.open(paymentUrl, "_blank").focus();
  };

  return (
    <div>
      <Flex
        direction="column"
        gap="4"
        style={{ background: "rgb(229, 241, 255)", padding: "2rem", borderRadius: 20, boxShadow: "rgba(0, 0, 0, 0.5) 0px 0px 40px 0px" }}
      >
        <Heading as="h1" style={{ fontSize: "2rem" }}>
          Payment
        </Heading>

        {loading ? (
          <p>Loading...</p>
        ) : (
          <Flex direction="column" gap="4">
            {/* <Heading as="h3">Make Payment</Heading> */}

            <Text as="span" style={{ fontWeight: "bolder" }}>
              Order Total:  {totalAmount}
            </Text>

            <Grid gap="2">
              <Flex direction="column" gap="2" style={{ maxWidth: 400 }}>
                <Text as="label" for="card_no">
                  Card Number
                </Text>
                <TextField.Root>
                  <TextField.Input
                    placeholder="Card No"
                    id="card_no"
                    name="cardNumber"
                    value={paymentDetails.cardNumber}
                    onChange={(e) => {
                      // alert(e.target.value)
                      updatePaymentDetails({
                        name: e.target.name,
                        value: e.target.value,
                      });
                    }}
                  />
                </TextField.Root>
              </Flex>

              <Flex direction="column" gap="2" style={{ maxWidth: 400 }}>
                <Text as="label" for="card_holder_name">
                  Card Holder Name
                </Text>
                <TextField.Root>
                  <TextField.Input
                    placeholder="Card Holder Name"
                    id="card_holder_name"
                    name="nameOnCard"
                    value={paymentDetails.nameOnCard}
                    onChange={(e) =>
                      updatePaymentDetails({
                        name: e.target.name,
                        value: e.target.value,
                      })
                    }
                  />
                </TextField.Root>
              </Flex>
            </Grid>

            <Flex gap="2">
              <Flex direction="column" gap="2" style={{ maxWidth: 150 }}>
                <Text as="label" for="cvv">
                  CVV
                </Text>
                <TextField.Root>
                  <TextField.Input
                    placeholder="CVV"
                    id="cvv"
                    name="cardSecurityCode"
                    value={paymentDetails.cardSecurityCode}
                    onChange={(e) =>
                      updatePaymentDetails({
                        name: e.target.name,
                        value: e.target.value,
                      })
                    }
                  />
                </TextField.Root>
              </Flex>

              <Flex direction="column" gap="2" style={{ maxWidth: 200 }}>
                <Text as="label" for="expiry_month">
                  Expiry Month
                </Text>
                <Select.Root
                  defaultValue="10"
                  onValueChange={(val) =>
                    updatePaymentDetails({
                      name: "cardExpiryMonth",
                      value: val,
                    })
                  }
                >
                  <Select.Trigger />
                  <Select.Content position="popper">
                    <Select.Item value="1">January</Select.Item>
                    <Select.Item value="2">February</Select.Item>
                    <Select.Item value="3">March</Select.Item>
                    <Select.Item value="4">April</Select.Item>
                    <Select.Item value="5">May</Select.Item>
                    <Select.Item value="6">June</Select.Item>
                    <Select.Item value="7">July</Select.Item>
                    <Select.Item value="8">August</Select.Item>
                    <Select.Item value="9">September</Select.Item>
                    <Select.Item value="10">October</Select.Item>
                    <Select.Item value="11">November</Select.Item>
                    <Select.Item value="12">December</Select.Item>
                  </Select.Content>
                </Select.Root>
              </Flex>

              <Flex direction="column" gap="2" style={{ maxWidth: 100 }}>
                <Text as="label" for="expiry_year">
                  Expiry Year
                </Text>
                <TextField.Root>
                  <TextField.Input
                    placeholder="Expiry Year"
                    id="expiry_year"
                    name="cardExpiryYear"
                    value={paymentDetails.cardExpiryYear}
                    onChange={(e) =>
                      updatePaymentDetails({
                        name: e.target.name,
                        value: e.target.value,
                      })
                    }
                  />
                </TextField.Root>
              </Flex>
            </Flex>

            <Flex gap="2" style={{alignItems: "center"}}>
                <img src={visa} alt="visa" style={{ height: 20 }} />
                <img src={masterCard} alt="visa" style={{  height: 30 }} />
            </Flex>

            <div>
              <Button
                size={"4"}
                disabled={checkoutDisabled}
                onClick={initiatePayment}
              >
                Initiate Payment
              </Button>
            </div>
          </Flex>
        )}
      </Flex>

      <div style={{ marginTop: 40 }}>
        <Flex
          direction="column"
          gap="4"
          style={{ background: "#cde3ff", padding: "2rem", borderRadius: 20 }}
        >
          <Text as="span" style={{ fontFamily: "monospace" }}>
            Order Id: {paymentDetails.orderId}
          </Text>
        </Flex>
      </div>
    </div>
  );
};

export default Checkout;
