import { createContext, useState, useEffect } from "react";
import {
  addCartItem,
  clearCartItems,
  getCartItems,
  removeCartItem,
} from "../../ApiClient/cart";

const CartContext = createContext({
  loading: false,
  setLoading: (loading) => {},
  items: [],
  totalAmount: 0,
  addItem: (item) => Promise,
  removeItem: (id) => Promise,
  clearCart: () => Promise,
});

export default CartContext;

export function CartProvider(props) {
  const [items, setCartItems] = useState([]);
  const [totalAmount, setTotalAmount] = useState(0);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    refreshCart();
  }, []);

  const refreshCart = () => {
    setLoading(true);
    getCartItems()
      .then((response) => {
        const items = response.data;
        const totalAmount = items.reduce((curNumber, item) => {
          return curNumber + item.amount;
        }, 0);
        setTotalAmount(totalAmount);
        setCartItems(response.data);
      })
      .finally(() => setLoading(false));
  };

  const addItem = (item) => {
    setLoading(true);
    addCartItem(item).then(() => {
      refreshCart();
    });
  };

  const removeItem = (id) => {
    setLoading(true);
    removeCartItem(id).then(() => {
      refreshCart();
    });
  };

  const clearCart = () => {
    setLoading(true);
    clearCartItems().then(() => {
      refreshCart([]);
    });
  };

  return (
    <CartContext.Provider
      value={{ loading, setLoading, items, totalAmount, addItem, removeItem, clearCart }}
    >
      {props.children}
    </CartContext.Provider>
  );
}
