import React, { useContext } from "react";
import { Flex, Heading, IconButton, Text } from "@radix-ui/themes";
import { PlusIcon, MinusIcon } from "@radix-ui/react-icons";
import CartContext from "../Cart/CartContext";

export default function Product({ product }) {
  const { items: cartItems, loading, addItem } = useContext(CartContext);

  const cartItem = cartItems.find((item) => item.productId === product.id);

  const quantity = cartItem ? cartItem.quantity : 0;

  const handleIncrease = async () => {
    addItem({
      productId: product.id,
      quantity: quantity + 1,
    });
  };

  const handleDecrease = async () => {
    addItem({
      productId: product.id,
      quantity: quantity - 1,
    });
  }

  return (
    <Flex direction="column" gap="6" style={{marginBottom: 20}}>
      <div>
        <img src={product.image} alt={product.name} style={{width: 200, borderRadius: 10}} />
      </div>
      <Flex direction="column" gap="2">
        <div style={{ flexGrow: "1" }}>
          <Heading as="h2">{product.name}</Heading>
          <Text as="p">{product.description}</Text>
          <Text as="span" style={{ fontWeight: "bolder"}}> {product.price}</Text>
        </div>

        <div>
          <Flex align="center" gap="3">
            <IconButton size="1" variant="soft" disabled={loading || (quantity <= 0)} onClick={handleDecrease}>
              <MinusIcon width="15" height="15" />
            </IconButton>
            <Heading as="h3">{quantity}</Heading>
            <IconButton size="1" variant="soft" disabled={loading} onClick={handleIncrease}>
              <PlusIcon width="15" height="15" />
            </IconButton>
          </Flex>
        </div>
      </Flex>
    </Flex>
  );
}
